import _import from '../../utils/_import'
import _importBasic from "../../utils/_importBasic"
// 首页
export default [{
  path: '/',
  component: _import('layout/index'),
  children: [{
    path: '/enter',
    component: _importBasic('enter'),
    children: [{
        path: 'entry',
        name: 'enterForm',
        component: _importBasic('enter/entry'),
        meta: {
          title: '考试报名',
          NavBarOption: {
            // leftText: '报名须知',
            title: '考试报名',
          },
        }
      },
      {
        path: 'notice',
        name: 'enterNotice',
        component: _importBasic('enter/notice'),
        meta: {
          title: '报名须知',
          NavBarOption: {
            title: '报名须知'
          },
        }
      },
      {
        path: 'informationConfirmation',
        name: 'enterInformationConfirmation',
        component: _importBasic('enter/informationConfirmation'),
        meta: {
          title: '确认信息',
          NavBarOption: {
            title: '确认信息'
          },
        }
      },
      {
        path: 'BookAnExaln',
        name: 'BookAnExaln',
        component: _importBasic('enter/BookAnExaln'),
        meta: {
          title: '确认信息',
          NavBarOption: {
            title: '确认信息'
          },
        }
      }
    ]
  }]
}]



