<template>
  <div id="BasicApp">
    <transition name="slide">
      <div class="app-container h100">
        <div class="m-h100" :class='`theme-${theme}`' v-if="isOpen">
          <vantNavBar ref="Navbar" 
           v-if="!!$route.meta.NavBarOption" 
           :options="$route.meta.NavBarOption" />
          <keep-alive>
            <router-view v-if="$route.meta.keepAlive" />
          </keep-alive>
          <router-view v-if="!$route.meta.keepAlive"></router-view>
        </div>

      </div>
    </transition>
  </div>
</template>
<script>
import Fingerprint2 from "fingerprintjs2"; // 引入fingerprintjs2
import { setStore, getStore, clearStore } from "@/utils/storage";
import { getClientFunction } from "@/utils/interface";
import { Toast } from "vant";

export default {
  name: "BasicApp",
  data() {
    return {
      theme: "daily", // daily dark
      isOpen: true, // todo
    };
  },

  mounted() {
    console.log(this.$route)
    // loadToken();
    // this.loadData();
    // this.fetchToken();
    // test
    // setStore("accessToken", 'Bearer eyJ0eXAiOiJKV1QiLCJhbGciOiJSUzI1NiJ9..f1LYKTLzMxo5BKgjYYpber5PQBLhZUl-ujBKLIm4amEKE1oYYsm9Tb7FyrP1SZ_y5IZqkz_6xilCXLmNNTCiE330BkE_bmmxcj-gcR6PNwsII1JMV3PSfiCTkZURisy1TwCV0Eu3San-oiVb-S76bjykNf6LQPXWPwjyGYT3h4bSIgaAIueuystf_ejzM1UzkKEBNibwEKUy8WZjOlNTBqj0T-6XOJjg8af3TVNoLubXuF8lPxJQOxTNriMU_6vx2ngSGxvkDy_d1ltpbO68r5lum5X_gI4lZECJRBiICOB8ycW6b2DlbLoCMPLBCcR8h3ADLCDYlaJiMauVmCZqxQ');
    // clearStore("accessToken")
  },

  methods: {
    fetchToken() {
      let data = {
        type: "token",
        params: "",
      };
      const self = this;
      try {
        getClientFunction(data, (res) => {
          setStore("accessToken", res);
          self.isOpen = true;
        }).bind(this);
      } catch (err) {}
    },

    loadData() {
      // 您不应在页面加载时或加载后直接运行指纹。 而是使用setTimeout或requestIdleCallback将其延迟几毫秒，以确保指纹一致。
      if (window.requestIdleCallback) {
        requestIdleCallback(() => {
          this.createFingerprint();
        });
      } else {
        setTimeout(() => {
          this.createFingerprint();
        }, 500);
      }
    },

    createFingerprint() {
      // 浏览器指纹
      const fingerprint = Fingerprint2.get((components) => {
        // 参数只有回调函数时，默认浏览器指纹依据所有配置信息进行生成
        const values = components.map((component) => component.value); // 配置的值的数组
        const murmur = Fingerprint2.x64hash128(values.join(""), 31); // 生成浏览器指纹
        // console.log(components);
        // console.log(values);
        console.log(murmur, "browserId");
        setStore("browserId", murmur); // 存储浏览器指纹，在项目中用于校验用户身份和埋点
      });
    },
  },
};
</script>
<style lang="scss" scoped>
@import "@/assets/css/theme.scss";
#BasicApp {
  height: 100%;
  margin: auto;
}
</style>


