import _import from '../../utils/_import'
import _importBasic from "../../utils/_importBasic"
// 首页
export default [{
  path: '/',
  component: _import('layout/index'),
  children: [{
    path: '/musicTheory',
    component: _importBasic('musicTheory'),
    children: [{
        path: 'entry',
        name: 'musicTheory',
        component: _importBasic('musicTheory/entry'),
        meta: {
          title: '考试报名',
          NavBarOption: {
            leftText: '报名须知',
            title: '考试报名',
          },
        }
      }
    ]
  }]
}]



