import _import from '../../utils/_import'
import _importBasic from "../../utils/_importBasic"

// 首页
export default [{
  path: '/',
  component: _import('layout/index'),
  children: [{
    path: '/list',
    component: _importBasic('list'),
    children: [{
        path: 'entry',
        name: 'listForm',
        component: _importBasic('list/entry'),
        meta: {
          title: '考试报名',
          NavBarOption: {
            leftText: '报名须知',
            title: '考试报名',
          },
        }
      },
    ]
  }]
}]



